@import '../../mixins';

.Section {
  padding: 40px 0 40px 0;
  border-top: 1px solid #e6e6e6;

  .section-title {
    text-align: center;
    color: #000;
    margin: 0 10px 0 10px;
    @include screen-mobile {
      font-size: 2.3em;
    }
    @include screen-mobile-small {
      font-size: 2.2em;
    }
  }
  
  .section-hr {
    padding: 0;
    border: none;
    border-top: solid 5px;
    text-align: center;
    max-width: 250px;
    margin: 25px auto 45px;
    border-color: #000;
    @include screen-mobile-small {
      border-top: solid 5px;
      margin-top: 20px;
      margin-bottom: 40px;
    }
    &:after {
      content: "\f005";
      font-family: "Font Awesome 5 Free";
      font-weight: 900;
      display: inline-block;
      font-size: 1.8em;
      padding: 0 .25em;
      position: absolute;
      margin-top: -22px;
      margin-left: -18px;
      background-color: #fff;
      color: #000;
      @include screen-mobile-small {
        font-size: 1.5em;
        margin-top: -18px;
        margin-left: -16px;
      }
    }
  }
}
